import './_team-stats.scss';
import React from 'react';
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretDown } from '@fortawesome/free-solid-svg-icons'
library.add(faCaretDown)

class TeamStats extends React.Component {
	addDefaultSrcTeam() {
		document.getElementById("image-team").style.backgroundImage = "url('https://fantasy-franchise.com/img/DEF.png')";
	}

	render() {
		var bgStyleA = {
			backgroundImage: `url(${this.props.data.logo})`
    };
		return (
		<div className="card mb-3 view-head">
			<div className="card-body">
				<div className="row">
				<div className="col-12">
							<div id="teamHeadSelector">
								<img src={this.props.data.logo} style={{ display: "none" }} onError={this.addDefaultSrcTeam} alt="team-logo"/>
								<div id="image-team" className="image" style={bgStyleA}></div>
								<select className="form-control" id="teamSelector" onChange={this.props.change}>
									{this.props.teams.map((team, index) => {
										if (team.status === 'active') {
											return <option key={'key' + index} value={team.id}>{team.name}</option>
										}
										else {
											return <option key={'key' + index} value={team.id}>Inactive - {team.name} </option>
										}

									})}
								</select><FontAwesomeIcon icon="caret-down" />

							</div>
							<strong>Team Name: </strong>{this.props.data.nickname}<br />
				</div>
				<div className="col-lg-6">
					<p>
						<strong>Founded: </strong>{this.props.data.founded}<br />
						<strong>Record: </strong> <span id="Record">{this.props.data.totalWins}-{this.props.data.totalLoses}-{this.props.data.totalTies}</span> <br />
						<strong>Biggest Winning Streak: </strong> <span id="big-win-streak">{this.props.data.stats.highestStreak}</span> <span className="streak-date">({this.props.data.stats.winStart} - {this.props.data.stats.winEnd})</span>
						<strong>Total Points: </strong><span id="total-points">{this.props.data.stats.totalPoints.toFixed(2)}</span><br />	
						<strong>Average Points: </strong><span id="avg-points">{this.props.data.averageScore.toFixed(2)}</span><br />	
						<strong>Highest Score: </strong><span id="highest-score">{this.props.data.stats.highestScore.toFixed(2)}</span> <span className="streak-date">({this.props.data.stats.highestScoreDate})</span> 
								<strong>Biggest Win Margin: </strong><span id="big-win-margin">{this.props.data.stats.highestWinMargin.toFixed(2)}</span> <span className="streak-date">({this.props.data.stats.highestWinMarginDate})</span> 
								<strong>Biggest Loss Margin: </strong><span id="big-loss-margin">-{this.props.data.stats.highestLossMargin.toFixed(2)}</span> <span className="streak-date">({this.props.data.stats.highestLossMarginDate})</span> 
					</p>
				</div> 
				<div className="col-lg-6">

					<p>
						<strong>Seasons: </strong> {this.props.data.seasons} <br />
						<strong>Current Streak: </strong> {this.props.data.stats.currentStreak} <br />
						<strong>Biggest Losing Streak: </strong> <span id="big-loss-streak">{this.props.data.stats.lowestStreak}</span> <span className="streak-date">({this.props.data.stats.lossStart} - {this.props.data.stats.lossEnd})</span>
						<strong>Total Points Against: </strong><span id="total-points-against">{this.props.data.stats.totalPointsAgainst.toFixed(2)}</span><br />	
						<strong>Average Points Against: </strong><span id="avg-points-against">{this.props.data.averageScoreAgaints.toFixed(2)}</span><br />
						<strong>Lowest Score: </strong><span id="loweset-score">{this.props.data.stats.lowestScore.toFixed(2)}</span> <span className="streak-date">({this.props.data.stats.lowestScoreDate})</span>
								<strong>Lowest Win Margin: </strong><span id="low-win-game">{this.props.data.stats.lowestWinMargin.toFixed(2)}</span> <span className="streak-date">({this.props.data.stats.lowestWinMarginDate})</span> 
								<strong>Lowest Loss Margin: </strong><span id="low-loss-game">-{this.props.data.stats.lowestLossMargin.toFixed(2)}</span> <span className="streak-date">({this.props.data.stats.lowestLossMarginDate})</span> 
						
					</p>
				</div>
				
			</div>
		</div> 
	</div>);
	}

}
   
          
          
export default TeamStats;