import _ from 'lodash';
import React from 'react';
import { graphql } from 'gatsby';
import Layout from "../components/layout";
import TeamStats from '../components/team-stats';
import Performance from '../components/Performance';
import Chart from '../components/chart';
import Breadcrumb from '../components/breadcrumb';

class IndexPage extends React.Component {
	constructor(props) {
		super(props);
		this.teamChangeHandler = this.teamChangeHandler.bind(this);
		this.rate = this.rate.bind(this);
		this.loadChart = this.loadChart.bind(this);
		this.loadTeamData = this.loadTeamData.bind(this);
		this.state = {
			teamSelected: {},
			allTeams: [],
			allGames: [],
			dataSet: {
				label: "Wins",
				lineTension: 0.3,
				backgroundColor: "rgba(2,117,216,0.2)",
				borderColor: "rgba(2,117,216,1)",
				pointRadius: 5,
				pointBackgroundColor: "rgba(2,117,216,1)",
				pointBorderColor: "rgba(255,255,255,0.8)",
				pointHoverRadius: 5,
				pointHoverBackgroundColor: "rgba(2,117,216,1)",
				pointHitRadius: 50,
				pointBorderWidth: 1,
				data: [], 
			}
		}
	}

	teamChangeHandler = (event) => {
		var teams = this.props.data.allDataJson.edges[0].node.owners;
		var indexTeam = 0;

		indexTeam = teams.findIndex(team => {
			return team.id === event.target.value;
		});

		// update actual state with modified object
		this.setState(
			{ teamSelected: this.loadTeamData(teams[indexTeam], this.state.allGames) },
			() => this.loadChart()
		)
	}

	rate = (win, loss, tie) => {
		var totalGames = parseInt(win) + parseInt(loss) + parseInt(tie);
		var winPercent = 100 * parseFloat(win + (tie * .5));
		if (win !== 0) {
			return Math.round(winPercent / totalGames);
		}
		else {
			return 0;
		}
	}

	loadTeamData = (team, games) => {
		//stats build upon scraped data collection
		
		var teamObject = team;
		var teamGames = [];
		var gamesByYear = [];
		var playOffWeeks = [];
		var teamPlayOffGames = [];
		var playOffYears = [];
		var beforePlayoffs = [];
		var bestPlacing = {
			rank: 30,
			year:''
		}

		// Obtener todos los games del team selecionado
		games.forEach((game) => {
			if ((game.team_a["0"].ownerId === team.id || game.team_b["0"].ownerId === team.id)) {
				teamGames.push(game);
			}
		})
		
		// Get best rank ever
		teamGames.forEach((game) => {
			if (game.team_a["0"].ownerId === team.id) {
				if (parseInt(game.team_a["0"].rank) < bestPlacing.rank) {
					bestPlacing.rank = parseInt(game.team_a["0"].rank);
					bestPlacing.year = _.replace(game.year," SEASON","");

				}
			}

			if (game.team_b["0"].ownerId === team.id) {
				if (parseInt(game.team_b["0"].rank) < bestPlacing.rank) {
					bestPlacing.rank = parseInt(game.team_b["0"].rank);
					bestPlacing.year = _.replace(game.year, " SEASON", "");
				}
			}
		});

		// get games by years and weeks
		games.forEach((game) => {
			if (gamesByYear.find(obj => obj.year === game.year)) {
				var currentYear = gamesByYear.find(obj => obj.year === game.year)
				if (currentYear.weeks.find(obj => obj.week === game.week)) {
					var currentWeek = currentYear.weeks.find(obj => obj.week === game.week)
					currentWeek.games.push(game);

				} else {
					currentYear.weeks.push({
						week: game.week,
						games: [game],
						year: game.year
					})
				}

			} else {
				gamesByYear.push({
					year: game.year,
					weeks: [{
						week: game.week,
						games: [game],
						year: game.year
					}]
				});
			}
		})

		// mark the playoff weeks and populate array
		gamesByYear.forEach((year) => {
			year.weeks.forEach((week) => {
				week.isPlayoff = false;
				week.games.forEach((game) => {
					if (game.playoffTierType !== 'NONE') {
						week.isPlayoff = true;
						week.games = _.filter(week.games, function (check) {
							return check.playoffTierType !== "NONE";
						});
					}
				});
				if (week.isPlayoff === true) {
					playOffWeeks.push(week);
				}
			});
		});

		// get team's playoff games
		playOffWeeks.forEach((week) => {
			week.games.forEach((game) => {
				if (game.team_a['0'].ownerId === (team.id && game.playoffTierType !== 'NONE') || game.team_b['0'].ownerId === (team.id && game.playoffTierType !== 'NONE')) {
					teamPlayOffGames.push(game);
				}
			});

			if (playOffYears.find(obj => obj.year === week.year)) {
				var yearObj = playOffYears.find(obj => obj.year === week.year);
				yearObj.games.push(week.games);

			} else {
				playOffYears.push({
					year: week.year,
					games: [week.games]
				})
			}
		});

		//get weeks before playoffs 
		gamesByYear.forEach((year, indexed) => {
			if (playOffYears.length-1 >= indexed) {
				var numberSelector = year.weeks.length - (playOffYears[indexed].games.length + 1) ;
				beforePlayoffs.push(year.weeks[numberSelector]);
			}
		});
		
		// get playoff stats

		teamObject.playOffWins = 0;
		teamObject.playOffLoses = 0;
		teamObject.playOffTies = 0;
		teamObject.playoffAppearances = _.sortedUniqBy(teamPlayOffGames, 'year').length;
		if (bestPlacing.rank === 1) {
			bestPlacing.rank = bestPlacing.rank +"st";
		}
		else if (bestPlacing.rank === 2) {
			bestPlacing.rank = bestPlacing.rank + "nd";
		}
		else if (bestPlacing.rank === 3) {
			bestPlacing.rank = bestPlacing.rank + "rd";
		}
		else {
			bestPlacing.rank = bestPlacing.rank + "th";
		}
		teamObject.bestPlacing = bestPlacing;


		// Get Playoffs wins, lose and ties
		teamPlayOffGames.forEach((game) => {
			if (game.team_a['0'].ownerId === team.id || game.team_b['0'].ownerId === team.id ) {

				/*
				console.log(game.week+ " ", game.year);
				console.log(game.winner);
				console.log("Team A: " + game.team_a[0].owner + " Team: " + game.team_a[0].score);
				console.log("Team B: " + game.team_b[0].owner + " Team: " + game.team_b[0].score);
				console.log("Separator ***********");
				*/

				if (
					(game.winner === 'HOME' && game.team_a['0'].ownerId === team.id) || 
					(game.winner === 'AWAY' && game.team_b['0'].ownerId === team.id)
					) {
					teamObject.playOffWins++;
				}
				else {
					teamObject.playOffLoses++;
				}
			}
		});

		teamObject.regularSeasonChampion = 0;
		teamObject.regularSeasonLastPlace = 0;

		//check regular season last places
		beforePlayoffs.forEach((week) => {
			week.games.forEach((game) => {
				if (game.team_a['0'].ownerId === team.id ) {
					const rank = parseInt(game.team_a[0].rank);

					if (rank === (week.games.length * 2)) {
						teamObject.regularSeasonLastPlace += 1;
					}
					if (rank === 1) {
						teamObject.regularSeasonChampion += 1;
					}
				}
				if (game.team_b['0'].ownerId === team.id ) {
					const rank = parseInt(game.team_b[0].rank);
					if (rank === (week.games.length * 2)) {
						teamObject.regularSeasonLastPlace += 1;
					}
					if (rank === 1) {
						teamObject.regularSeasonChampion += 1;
					}
				}
			});
		});

		teamObject.championshipAppearance = 0;
		teamObject.championshipWins = 0;
		teamObject.championshipLoses = 0;
		teamObject.championshipTies = 0;
		
		// championship stats 
		playOffYears.forEach((year) => {
			// Get last week which is the championship week
			var champWeek = year.games[year.games.length - 1]

			champWeek.forEach((game, index) => {
				if (
					(game.team_a[0].ownerId === team.id && game.playoffTierType === 'WINNERS_BRACKET') ||
					(game.team_b[0].ownerId === team.id && game.playoffTierType === 'WINNERS_BRACKET')
					) {
					teamObject.championshipAppearance++;
				}
				// ESPN Only game winner
				if (
					(game.winner === 'HOME' && game.team_a[0].ownerId === team.id && game.playoffTierType === 'WINNERS_BRACKET')  ||
					(game.winner === 'AWAY' && game.team_b[0].ownerId === team.id && game.playoffTierType === 'WINNERS_BRACKET')
				) {
					teamObject.championshipWins++;	
				}
				else {
					teamObject.championshipLoses++;
				}
			})
		})

		//find year of first occurance
		teamObject.founded = teamGames[0].year;

		for (let ii = 0; ii < teamGames.length; ii++) {
			const week = teamGames[ii].week.split(" ");
			teamGames[ii].date = "01/" + week[1] + "/" + parseInt(games[ii].year);
		}

		var nikcindex = teamGames.findIndex((game) => {
			if (game.team_a["0"].ownerId === team.id || game.team_b["0"].ownerId === team.id) {
				return game.year;
			}
			return null;
		});

		if (teamGames[nikcindex].team_a['0'].ownerId === team.id) {
			teamObject.nickname = teamGames[nikcindex].team_a['0'].name
		} else if (teamGames[nikcindex].team_b['0'].ownerId === team.id) {
			teamObject.nickname = teamGames[nikcindex].team_b['0'].name
		}

		teamGames.sort(function (a, b) {
			a = new Date(a.date);
			b = new Date(b.date);
			return a > b ? -1 : a < b ? 1 : 0;
		});

		teamGames.reverse();

		// yearly history build up 

		var yearly = [];
		teamObject.totalWins = 0;
		teamObject.totalLoses = 0;
		teamObject.totalTies = 0;

		teamGames.forEach((game) => {
			
			if (
				(game.winner === 'HOME' && game.team_a['0'].ownerId === team.id) ||
				(game.winner === 'AWAY' && game.team_b['0'].ownerId === team.id)
			) {
				teamObject.totalWins++;

				if (yearly.find(obj => obj.year === parseInt(game.year))) {
					yearly.forEach((yearsg, indi) => {
						if (yearsg.year === parseInt(game.year)) {
							yearly[indi].wins++
							yearly[indi].points += 10;
						}
					})
				} else {
					yearly.push({
						year: parseInt(game.year),
						wins: 1,
					});
				}
			} else if (parseFloat(game.team_a[0].score) === parseFloat(game.team_b[0].score)) {
				teamObject.totalTies++;
			} else {
				teamObject.totalLoses++;
			}
		})

		teamObject.yearly = yearly.sort((a, b) => a.year - b.year);

		var yearsActive = [];
		yearly.forEach((year) => {
			yearsActive.push(year.year);
		})
		teamObject.yearsActive = yearsActive.sort((a, b) => a - b);

		// number of seasons
		teamObject.seasons = yearly.length;

		// logo 
		teamObject.logo = team.image

		// current stats 
		teamObject.winStreak = 0;
		teamObject.lossStreak = 0;
		teamObject.lossStart = "";
		teamObject.lossEnd = "";
		teamObject.winStart = "";
		teamObject.winEnd = "";
		teamObject.highWinMargin = 0;
		teamObject.lowWinMargin = 0;
		teamObject.highLossMargin = 0;
		teamObject.lowLossMargin = 0;
		teamObject.stats = {
			highestStreak: 0,
			lowestStreak: 0,
			currentStreak: 0,
			highestScore: 0,
			highestScoreDate: "",
			lowestScore: 0,
			lowestScoreDate: "",
			highestWinMargin: 0,
			lowestWinMargin: 0,
			highestWinMarginDate: "",
			lowestWinMarginDate: "",
			highestLossMargin: 0,
			lowestLossMargin: 0,
			highestLossMarginDate: "",
			lowestLossMarginDate: "",
			totalPoints: 0,
			totalPointsAgainst: 0,
			lossStart: "",
			lossEnd: "",
			winStart: "",
			winEnd: "",
		}

		for (let i = 0; i < teamGames.length; i++) {
			const week = teamGames[i].week.split(" ");
			teamGames[i].date = "01/" + week[1] + "/" + parseInt(teamGames[i].year);
		}

		teamGames.sort(function (a, b) {
			a = new Date(a.date);
			b = new Date(b.date);
			return a > b ? -1 : a < b ? 1 : 0;
		});

		teamGames.reverse();

		teamGames.forEach((game, inde) => {
			
			if (
				(game.team_a[0].ownerId === team.id && parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score))
				||
				(game.team_b[0].ownerId === team.id && parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score))
				) {
				teamObject.lowWinMargin = Math.abs(parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score));
				teamObject.highWinMargin = Math.abs(parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score));
				
				if (teamObject.lossStreak !== 0) teamObject.lossEnd = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.winStreak === 0) teamObject.winStart = game.year.split(" ")[0] + " " + game.week;

				if (teamObject.lossStreak === teamObject.stats.lowestStreak && teamObject.lossStreak !== 0) {
					teamObject.stats.lossEnd = teamObject.lossEnd;
					teamObject.lossEnd = "";
				}
				teamObject.winStreak++;
				teamObject.lossStreak = 0;

			} else if (
				(game.team_a[0].ownerId === team.id && parseFloat(game.team_a[0].score) < parseFloat(game.team_b[0].score))
				||
				(game.team_b[0].ownerId === team.id && parseFloat(game.team_b[0].score) < parseFloat(game.team_a[0].score))
				) {
				
				teamObject.lowLossMargin = Math.abs(parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score));
				teamObject.highLossMargin = Math.abs(parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score));

				if (teamObject.winStreak !== 0) teamObject.winEnd = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.lossStreak === 0) teamObject.lossStart = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.winStreak === teamObject.stats.highestStreak && teamObject.winStreak !== 0) {
					teamObject.stats.winEnd = teamObject.winEnd;
					teamObject.winEnd = "";
				}

				teamObject.lossStreak++;
				teamObject.winStreak = 0;
			} else {
				if (teamObject.winStreak === 0) teamObject.winStart = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.lossStreak !== 0) teamObject.lossEnd = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.winStreak === teamObject.stats.highestStreak && teamObject.winStreak !== 0) {
					teamObject.stats.winEnd = teamObject.winEnd;
					teamObject.winEnd = "";
				}
				teamObject.lossStreak = 0;
				teamObject.winStreak = 0;
			}

			// set currentStreak
			if (teamObject.winStreak === 0) {
				teamObject.stats.currentStreak = teamObject.lossStreak + "L";
			}
			else {
				teamObject.stats.currentStreak = teamObject.winStreak + "W";
			}

			// set the master stats var
			if (teamObject.winStreak >= teamObject.stats.highestStreak) {
				teamObject.stats.highestStreak = teamObject.winStreak;
				teamObject.stats.winStart = teamObject.winStart;
				if (teamObject.stats.currentStreak === teamObject.stats.highestStreak + "W") {
					teamObject.stats.winEnd = "Current";
				}
			}

			if (teamObject.lossStreak >= teamObject.stats.lowestStreak) {
				teamObject.stats.lowestStreak = teamObject.lossStreak;
				teamObject.stats.lossStart = teamObject.lossStart;
				if (teamObject.stats.currentStreak === teamObject.stats.lowestStreak + "L") {
					teamObject.stats.lossEnd = "Current";
				}
			}

			// Check Highest score
			if (parseFloat(game.team_a[0].score) > teamObject.stats.highestScore && game.team_a[0].ownerId === team.id) {
				teamObject.stats.highestScore = parseFloat(game.team_a[0].score);
				teamObject.stats.highestScoreDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
			}

			if (parseFloat(game.team_b[0].score) > teamObject.stats.highestScore && game.team_b[0].ownerId === team.id) {
				teamObject.stats.highestScore = parseFloat(game.team_b[0].score);
				teamObject.stats.highestScoreDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
			}

			// Check lowest score 

			if (inde === 0 && game.team_a[0].ownerId === team.id) {
				teamObject.stats.lowestScore = parseFloat(game.team_a[0].score);
				teamObject.stats.lowestScoreDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
			}

			if (inde === 0 && game.team_b[0].ownerId === team.id) {
				teamObject.stats.lowestScore = parseFloat(game.team_b[0].score);
				teamObject.stats.lowestScoreDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
			}

			if (parseFloat(game.team_a[0].score) < teamObject.stats.lowestScore && game.team_a[0].ownerId === team.id) {
				teamObject.stats.lowestScore = parseFloat(game.team_a[0].score);
				teamObject.stats.lowestScoreDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
			}

			if (parseFloat(game.team_b[0].score) < teamObject.stats.lowestScore && game.team_b[0].ownerId === team.id) {
				teamObject.stats.lowestScore = parseFloat(game.team_b[0].score);
				teamObject.stats.lowestScoreDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
			}

			// Check Highest margin 
			if (teamObject.highWinMargin > teamObject.stats.highestWinMargin ) {
				teamObject.stats.highestWinMargin = teamObject.highWinMargin;
				if (game.team_a[0].ownerId === team.id) {
					teamObject.stats.highestWinMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
				}
				else {
					teamObject.stats.highestWinMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
				}
			}
			
			if (teamObject.highLossMargin > teamObject.stats.highestLossMargin) {
				teamObject.stats.highestLossMargin = teamObject.highLossMargin;
				if (game.team_a[0].ownerId === team.id) {
					teamObject.stats.highestLossMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
				}
				else {
					teamObject.stats.highestLossMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
				}
			}

			// Check Lowest margin 

			if (teamObject.stats.lowestWinMargin === 0 && teamObject.lowWinMargin > 0) {
				teamObject.stats.lowestWinMargin = teamObject.lowWinMargin;
				if (game.team_a[0].ownerId === team.id) {
					teamObject.stats.lowestWinMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
				}
				else {
					teamObject.stats.lowestWinMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
				}
			}
			if (teamObject.lowWinMargin < teamObject.stats.lowestWinMargin && teamObject.lowWinMargin > 0) {
				teamObject.stats.lowestWinMargin = teamObject.lowWinMargin;
				if (game.team_a[0].ownerId === team.id) {
					teamObject.stats.lowestWinMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
				}
				else {
					teamObject.stats.lowestWinMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
				}
			}

			if (teamObject.stats.lowestLossMargin === 0 && teamObject.lowLossMargin > 0) {
				teamObject.stats.lowestLossMargin = teamObject.lowLossMargin;
				if (game.team_a[0].ownerId === team.id) {
					teamObject.stats.lowestLossMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
				}
				else {
					teamObject.stats.lowestLossMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
				}
			}

			if (teamObject.lowLossMargin < teamObject.stats.lowestLossMargin && teamObject.lowLossMargin > 0) {
				teamObject.stats.lowestLossMargin = teamObject.lowLossMargin;
				if (game.team_a[0].ownerId === team.id) {
					teamObject.stats.lowestLossMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_b[0].owner;
				}
				else {
					teamObject.stats.lowestLossMarginDate = game.year.split(" ")[0] + " " + game.week + " vs " + game.team_a[0].owner;
				}
			}

			if (game.team_a[0].ownerId === team.id) {
				teamObject.stats.totalPoints += parseFloat(game.team_a[0].score);
				teamObject.stats.totalPointsAgainst += parseFloat(game.team_b[0].score);
			}

			if (game.team_b[0].ownerId === team.id) {
				teamObject.stats.totalPoints += parseFloat(game.team_b[0].score);
				teamObject.stats.totalPointsAgainst += parseFloat(game.team_a[0].score);
			}
		})

		teamObject.averageScore = teamObject.stats.totalPoints / teamGames.length;
		teamObject.averageScoreAgaints = teamObject.stats.totalPointsAgainst / teamGames.length;

		return teamObject
	}

	loadChart = () => {
		const dataSetCopy = { ...this.state.dataSet };
		dataSetCopy.data = []
		for (var i = 0; i < this.state.teamSelected.yearly.length; i++) {
			dataSetCopy.data.push(this.state.teamSelected.yearly[i].wins)
		}

		this.setState({ dataSet: dataSetCopy })
	}

	UNSAFE_componentWillMount() {
		const owners = this.props.data.allDataJson.edges[0].node.owners;
		var team = owners[0];
		const years = this.props.data.allDataJson.edges[0].node.years;
		var games = [];
		years.forEach((year) => {
			year.weeks_games.forEach((week) => {
				games.push(...week.games);
			})
		})
		this.setState({ allGames: games })
		this.setState({ allTeams: owners })
		this.setState({ teamSelected: this.loadTeamData(team, games) })

	}

	componentDidMount() {
		this.setState({ teamSelected: this.loadTeamData(this.state.teamSelected, this.state.allGames) })
		this.loadChart();
	}

	render() {

		const chartData = {
			labels: this.state.teamSelected.yearsActive,
			datasets: [this.state.dataSet]
		}
		return (
			<Layout >
				<main className="container-fluid">
					<Breadcrumb title="Franchise Stats" />
					<div className="row">
						<div className="col-xl-5 col-lg-12">
							<TeamStats data={this.state.teamSelected} teams={this.state.allTeams} change={(event) => this.teamChangeHandler(event)} />
						</div>
						<div className="col-xl-7 col-lg-12">
							<Performance
								title="Regular Season"
								rate={this.rate((this.state.teamSelected.totalWins - this.state.teamSelected.playOffWins), (this.state.teamSelected.totalLoses - this.state.teamSelected.playOffLoses), (this.state.teamSelected.totalTies - this.state.teamSelected.playOffTies))}
								win={this.state.teamSelected.totalWins - this.state.teamSelected.playOffWins}
								loss={this.state.teamSelected.totalLoses - this.state.teamSelected.playOffLoses}
								tie={this.state.teamSelected.totalTies - this.state.teamSelected.playOffTies}
								boxa={{ title: 'Regular Season Champion', content: this.state.teamSelected.regularSeasonChampion }}
								boxb={{ title: 'Best Placing', content: this.state.teamSelected.bestPlacing.rank +" ("+ this.state.teamSelected.bestPlacing.year + ")" }}
								boxc={{ title: 'Last Place', content: this.state.teamSelected.regularSeasonLastPlace }}
							/>
							<Performance
								title="Playoff"
								rate={this.rate(this.state.teamSelected.playOffWins, this.state.teamSelected.playOffLoses, this.state.teamSelected.playOffTies)}
								win={this.state.teamSelected.playOffWins}
								loss={this.state.teamSelected.playOffLoses}
								tie={this.state.teamSelected.playOffTies}
								boxa={{ title: 'Playoffs Appearances', content: this.state.teamSelected.playoffAppearances }}
								boxb={{ title: 'Championships Appearance', content: this.state.teamSelected.championshipAppearance }}
								boxc={{ title: 'Championships', content: this.state.teamSelected.championshipWins }}
							/>
						</div>
					</div>
					<Chart data={chartData} />
				</main>
			</Layout>
			);
	}
}
export default IndexPage;

export const pageQuery = graphql`
  query IndexQuery {
		allDataJson {
			edges {
				node {
					owners {
						name
						fantasyname
						id
						image
						status
					}
					years{
						weeks_games {
							games {
								week
								playoffTierType
								winner
								team_a {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								team_b {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								year
							}
						}
					}
				} 
			}
		}
}
`;
